import Vue from 'vue'

import {
  Button,
  Select,
  Pagination,
  Card,
  Loading,
  Radio,
  RadioGroup,
  Input,
  Dialog,
  Form,
  FormItem,
  Collapse,
  CollapseItem,
  Message,
  Upload
} from 'element-ui'

Vue.use(Button)
Vue.use(Select)
Vue.use(Pagination)
Vue.use(Card)
Vue.use(Loading.directive)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(Input)
Vue.use(Dialog)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Collapse)
Vue.use(CollapseItem)
Vue.use(Upload)

Vue.prototype.$message = Message



